.main-container {
  text-align: center;
}

.search-form {
  width: 638px;
  margin: 0 auto;
  position: relative;
}

.icon-google-logo, .icon-oddity-logo {
  width: 272px;
  margin-top: 92px;
}

.hidden {
  display: none;
}

.search-input {
  box-sizing: border-box;
  width: 100%;
  min-height: 50px;
  border: 1px solid #dfe1e5;
  border-radius: 24px;
  outline: none;
  margin-bottom: 30px;
  padding: 10px 130px 10px 45px;
  font-size: 16px;
  transition: all .2s ease-in-out;
}

.search-input:focus, .search-input:hover {
  border-color: rgba(0, 0, 0, 0);
  box-shadow: 0 1px 6px rgba(32, 33, 36, .28);
}

.search-icon {
  fill: #a1a7ac;
  position: absolute;
  top: 15px;
  left: 20px;
}

.button-input-wrapper {
  display: flex;
  position: absolute;
  top: 4px;
  right: 20px;
}

.button-input {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.button-input-wrapper > :not(:last-child) {
  margin-right: 10px;
}

.button-search-wrapper {
  justify-content: center;
  margin-bottom: 30px;
  display: flex;
}

.button-search {
  margin-right: 20px;
}

.button-search, .button-success {
  color: #3c4043;
  min-width: 54px;
  text-align: center;
  cursor: pointer;
  background-color: #f8f9fa;
  border: 1px solid #f8f9fa;
  border-radius: 4px;
  padding: 5px 16px;
  font-size: 14px;
  line-height: 27px;
  transition: all .2s ease-in-out;
}

.button-search:hover, .button-success:hover {
  color: #202124;
  background-color: #f8f9fa;
  border: 1px solid #dadce0;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
}

.button-search:focus, .button-success:focus {
  border: 1px solid #4285f4;
  outline: none;
}

.language-wrapper {
  margin-left: auto;
  margin-right: auto;
}

.language-text, .language-link {
  color: #4d5156;
  font-family: Arial, sans-serif;
  font-size: 14px;
}

.language-link:hover {
  text-decoration: underline;
}

.language-link:visited {
  color: #681da8;
}

